/*Game CSS*/

.gameContainer {
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
}

.scorerValue {
    color: #f9f6f2;
    font-weight: bold;
}

.scorer {
    margin: 10px 10px;
    height: 5vh;
    width: 19vh;
    background-color: #007bff;
    border-color: #007bff;
    border-radius: 5px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    font-size: 3vh;
}

.dashboard {
    /* position:relative; */
    top: 5%;
    left: 5vw;
    width: 40vw;
    height: 40vw;
    background: #bbada0;
}

.cell {
    height: 25%;
    width: 25%;
    background: #cdc1b4;
    border-radius: 100px;
}

.number {
    height: 100%;
    width: 100%;
    border-radius: 5px;
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-transition: -webkit-transform 0.08s linear;
    transition: -webkit-transform 0.08s linear;
    transition: transform 0.08s linear;
    transition: transform 0.08s linear, -webkit-transform 0.08s linear;
    -webkit-transform: scale(1);
            transform: scale(1);
    background: red;
    font-size: 27px;
    color: #f9f6f2;
    font-weight: bold;
}

