/*Landing Page CSS*/

.headingStyle {
    width: 100%;
    height: 100%;
    background: #FF7777;
    -webkit-font-smoothing: antialiased;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding-bottom: 34vh;
}

.headingStyle h1 {
    height: 100px;
}

.headingStyle h1 span {
    position: relative;
    top: 20px;
    display: inline-block;
    -webkit-animation: bounce .3s ease infinite alternate;
            animation: bounce .3s ease infinite alternate;
    font-family: 'Titan One', cursive;
    font-size: 14vh;
    color: #FFF;
    text-shadow: 0 1px 0 #CCC, 0 2px 0 #CCC, 0 3px 0 #CCC, 0 4px 0 #CCC, 0 5px 0 #CCC, 0 6px 0 transparent, 0 7px 0 transparent, 0 8px 0 transparent, 0 9px 0 transparent, 0 10px 10px rgba(0, 0, 0, .4);
}

.headingStyle h1 span:nth-child(2) {
    -webkit-animation-delay: .1s;
            animation-delay: .1s;
}

.headingStyle h1 span:nth-child(3) {
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
}

.headingStyle h1 span:nth-child(4) {
    -webkit-animation-delay: .3s;
            animation-delay: .3s;
}

.headingStyle h1 span:nth-child(5) {
    -webkit-animation-delay: .4s;
            animation-delay: .4s;
}

.headingStyle h1 span:nth-child(6) {
    -webkit-animation-delay: .5s;
            animation-delay: .5s;
}

.headingStyle h1 span:nth-child(7) {
    -webkit-animation-delay: .6s;
            animation-delay: .6s;
}

.headingStyle h1 span:nth-child(8) {
    -webkit-animation-delay: .7s;
            animation-delay: .7s;
}

.headingStyle h1 span:nth-child(9) {
    -webkit-animation-delay: .8s;
            animation-delay: .8s;
}

.headingStyle h1 span:nth-child(10) {
    -webkit-animation-delay: .9s;
            animation-delay: .9s;
}

.headingStyle h1 span:nth-child(11) {
    -webkit-animation-delay: 1.0s;
            animation-delay: 1.0s;
}

.headingStyle h1 span:nth-child(12) {
    -webkit-animation-delay: 1.1s;
            animation-delay: 1.1s;
}

.headingStyle h1 span:nth-child(13) {
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
}

.headingStyle h1 span:nth-child(14) {
    -webkit-animation-delay: 1.3s;
            animation-delay: 1.3s;
}

.headingStyle h1 span:nth-child(15) {
    -webkit-animation-delay: 1.4s;
            animation-delay: 1.4s;
}

.headingStyle h1 span:nth-child(16) {
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
}

@-webkit-keyframes bounce {
    100% {
        top: -20px;
        text-shadow: 0 1px 0 #CCC, 0 2px 0 #CCC, 0 3px 0 #CCC, 0 4px 0 #CCC, 0 5px 0 #CCC, 0 6px 0 #CCC, 0 7px 0 #CCC, 0 8px 0 #CCC, 0 9px 0 #CCC, 0 50px 25px rgba(0, 0, 0, .2);
    }
}

@keyframes bounce {
    100% {
        top: -20px;
        text-shadow: 0 1px 0 #CCC, 0 2px 0 #CCC, 0 3px 0 #CCC, 0 4px 0 #CCC, 0 5px 0 #CCC, 0 6px 0 #CCC, 0 7px 0 #CCC, 0 8px 0 #CCC, 0 9px 0 #CCC, 0 50px 25px rgba(0, 0, 0, .2);
    }
}

